import { Card, Button, Space, Alert, Input, Typography } from 'antd';
import { CopyOutlined, CodeOutlined, ReloadOutlined } from '@ant-design/icons';
import PropTypes from 'prop-types';

const { TextArea } = Input;
const { Text } = Typography;

const ScriptDisplayCard = ({ script, onCopy, onGenerate, loading }) => {
  return (
    <Card 
      title={<><CodeOutlined /> Generated Deletion Script</>}
      extra={
        <Space>
          <Button 
            icon={<ReloadOutlined />}
            onClick={onGenerate}
            loading={loading}
          >
            Generate Script
          </Button>
          <Button 
            type="primary" 
            icon={<CopyOutlined />}
            onClick={onCopy}
            disabled={!script || script === '# No resources to delete'}
          >
            Copy Script
          </Button>
        </Space>
      }
    >
      <Space direction="vertical" size="middle" style={{ width: '100%' }}>
        <Alert
          message="How to use this script"
          description={
            <ol style={{ marginBottom: 0, paddingLeft: 20 }}>
              <li>Click "Copy Script" button above</li>
              <li>Save to a file (e.g., <code>delete-resources.sh</code>)</li>
              <li>Make it executable: <code>chmod +x delete-resources.sh</code></li>
              <li>Review the script carefully</li>
              <li>Execute: <code>./delete-resources.sh</code></li>
              <li>After deletion, please perform the "Empty State File" action at the bottom to clear the state file</li>
            </ol>
          }
          type="info"
          showIcon
        />

        <TextArea
          value={script}
          readOnly
          rows={20}
          style={{ 
            fontFamily: 'Monaco, Menlo, "Ubuntu Mono", monospace',
            fontSize: 13,
            background: '#1e1e1e',
            color: '#d4d4d4',
            border: '1px solid #333'
          }}
        />

        <Text type="secondary" style={{ fontSize: 12 }}>
          Script contains {script.split('\n').length} lines
        </Text>
      </Space>
    </Card>
  );
};

ScriptDisplayCard.propTypes = {
  script: PropTypes.string.isRequired,
  onCopy: PropTypes.func.isRequired,
  onGenerate: PropTypes.func,
  loading: PropTypes.bool,
};

export default ScriptDisplayCard;
