import { Modal, Space, Alert, Input, Typography } from 'antd';
import { WarningOutlined } from '@ant-design/icons';
import PropTypes from 'prop-types';

const { Text, Paragraph } = Typography;

const ConfirmationModal = ({
  visible,
  onOk,
  onCancel,
  confirmText,
  onConfirmTextChange,
  loading,
  title = 'Confirm Action',
  okText = 'Confirm',
  requiredText = 'DELETE ALL',
  warningTitle,
  warningDescription,
}) => {
  return (
    <Modal
      title={
        <Space>
          <WarningOutlined style={{ color: '#ff4d4f' }} />
          <span>{title}</span>
        </Space>
      }
      open={visible}
      onOk={onOk}
      onCancel={onCancel}
      okText={okText}
      cancelText="Cancel"
      okButtonProps={{ 
        danger: true, 
        disabled: confirmText !== requiredText,
        loading: loading
      }}
      confirmLoading={loading}
      width={600}
      maskClosable={false}
    >
      <Space direction="vertical" size="large" style={{ width: '100%' }}>
        {warningTitle && (
          <Alert
            message={warningTitle}
            description={warningDescription}
            type="error"
            showIcon
          />
        )}

        <div>
          <Paragraph strong style={{ marginBottom: 8 }}>
            Type <Text code strong style={{ color: '#ff4d4f' }}>{requiredText}</Text> to confirm:
          </Paragraph>
          <Input
            placeholder={`Type ${requiredText} to confirm`}
            value={confirmText}
            onChange={(e) => onConfirmTextChange(e.target.value)}
            onPressEnter={onOk}
            size="large"
            status={confirmText && confirmText !== requiredText ? 'error' : ''}
            autoFocus
          />
          {confirmText && confirmText !== requiredText && (
            <Text type="danger" style={{ fontSize: 12, marginTop: 4, display: 'block' }}>
              Please type exactly "{requiredText}"
            </Text>
          )}
        </div>
      </Space>
    </Modal>
  );
};

ConfirmationModal.propTypes = {
  visible: PropTypes.bool.isRequired,
  onOk: PropTypes.func.isRequired,
  onCancel: PropTypes.func.isRequired,
  confirmText: PropTypes.string.isRequired,
  onConfirmTextChange: PropTypes.func.isRequired,
  loading: PropTypes.bool,
  title: PropTypes.string,
  okText: PropTypes.string,
  requiredText: PropTypes.string,
  warningTitle: PropTypes.node,
  warningDescription: PropTypes.node,
};

export default ConfirmationModal;
