import { Card, Tag, Typography } from 'antd';
import PropTypes from 'prop-types';

const { Text } = Typography;

const ResourceCard = ({ resource, index }) => {
  // Get display name: prefer resourceName or name from properties, fallback to resourceId
  const displayName = resource.properties?.resourceName || 
                      resource.properties?.name || 
                      resource.properties?.resourceId || 
                      resource.id;

  return (
    <Card 
      size="small"
      style={{ 
        background: '#f8f9fa',
        border: '1px solid #e9ecef'
      }}
    >
      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start' }}>
        <div style={{ flex: 1 }}>
          <div style={{ display: 'flex', alignItems: 'center', gap: 8, marginBottom: 8 }}>
            <Tag color="blue">{index + 1}</Tag>
            <Text strong style={{ fontSize: 15 }}>
              {displayName}
            </Text>
            <Tag color="default">{resource.type}</Tag>
            {resource.status && (
              <Tag color={resource.status === 'available' || resource.status === 'running' || resource.status === 'created' ? 'green' : 'orange'}>
                {resource.status}
              </Tag>
            )}
          </div>
          
          <div style={{ marginLeft: 8 }}>
            <Text type="secondary" style={{ fontSize: 12 }}>
              <strong>ID:</strong> {resource.id}
            </Text>
            {resource.region && (
              <Text type="secondary" style={{ fontSize: 12, marginLeft: 16 }}>
                <strong>Region:</strong> {resource.region}
              </Text>
            )}
          </div>

          {resource.dependencies && resource.dependencies.length > 0 && (
            <div style={{ marginTop: 8, marginLeft: 8 }}>
              <Text type="secondary" style={{ fontSize: 12 }}>
                <strong>Dependencies:</strong> {resource.dependencies.join(', ')}
              </Text>
            </div>
          )}

          {resource.dependents && resource.dependents.length > 0 && (
            <div style={{ marginTop: 4, marginLeft: 8 }}>
              <Text type="secondary" style={{ fontSize: 12 }}>
                <strong>Dependents:</strong> {resource.dependents.join(', ')}
              </Text>
            </div>
          )}
        </div>
      </div>
    </Card>
  );
};

ResourceCard.propTypes = {
  resource: PropTypes.shape({
    id: PropTypes.string.isRequired,
    name: PropTypes.string,
    type: PropTypes.string.isRequired,
    status: PropTypes.string,
    region: PropTypes.string,
    dependencies: PropTypes.arrayOf(PropTypes.string),
    dependents: PropTypes.arrayOf(PropTypes.string),
  }).isRequired,
  index: PropTypes.number.isRequired,
};

export default ResourceCard;
