import { Tag, Tooltip, Typography } from 'antd';
import PropTypes from 'prop-types';

const { Text } = Typography;

const DeletionOrderTags = ({ deletionOrder, resources }) => {
  return (
    <div>
      <Text strong style={{ fontSize: 14 }}>Deletion Order:</Text>
      <div style={{ marginTop: 8, display: 'flex', flexWrap: 'wrap', gap: 8 }}>
        {deletionOrder.map((resourceId, index) => {
          const resource = resources.find(r => r.id === resourceId);
          // Get display name: prefer resourceName or name from properties, fallback to resourceId
          const displayName = resource?.properties?.resourceName || 
                              resource?.properties?.name || 
                              resource?.properties?.resourceId || 
                              resourceId;
          return (
            <Tooltip 
              key={resourceId} 
              title={`${resource?.type || 'Unknown'}: ${displayName}`}
            >
              <Tag color="blue" style={{ margin: 0 }}>
                {index + 1}. {displayName}
              </Tag>
            </Tooltip>
          );
        })}
      </div>
    </div>
  );
};

DeletionOrderTags.propTypes = {
  deletionOrder: PropTypes.arrayOf(PropTypes.string).isRequired,
  resources: PropTypes.arrayOf(PropTypes.object).isRequired,
};

export default DeletionOrderTags;
